import { NextRequest, NextResponse } from "next/server";
import CategoryGroup from "@/models/CategoryGroup";
import connectDB from "../../../lib/dbConnect";

export async function GET(request: NextRequest) {
    try {
        await connectDB();
        
        const { searchParams } = new URL(request.url);
        const page = parseInt(searchParams.get("page") || "1");
        const limit = parseInt(searchParams.get("limit") || "10");
        const search = searchParams.get("search") || "";
        const isActive = searchParams.get("isActive");

        const query: any = {};
        
        if (search) {
            query.$or = [
                { name: { $regex: search, $options: "i" } },
                { code: { $regex: search, $options: "i" } },
                { note: { $regex: search, $options: "i" } }
            ];
        }
        
        if (isActive !== null && isActive !== undefined) {
            query.isActive = isActive === "true";
        }

        const skip = (page - 1) * limit;
        
        const [categoryGroups, total] = await Promise.all([
            CategoryGroup.find(query)
                .sort({ createdAt: -1 })
                .skip(skip)
                .limit(limit),
            CategoryGroup.countDocuments(query)
        ]);

        return NextResponse.json({
            success: true,
            data: categoryGroups,
            pagination: {
                page,
                limit,
                total,
                pages: Math.ceil(total / limit)
            }
        });
    } catch (error) {
        console.error("Error fetching category groups:", error);
        return NextResponse.json(
            { success: false, error: "Kh√¥ng th·ªÉ l·∫•y danh s√°ch nh√≥m h·∫°ng m·ª•c" },
            { status: 500 }
        );
    }
}

export async function POST(request: NextRequest) {
    try {
        await connectDB();
        
        const body = await request.json();
        const { name, code, note } = body;

        if (!name || !code) {
            return NextResponse.json(
                { success: false, error: "T√™n v√† m√£ nh√≥m l√† b·∫Øt bu·ªôc" },
                { status: 400 }
            );
        }

        // Check if code already exists
        const existingGroup = await CategoryGroup.findOne({ code: code.toUpperCase() });
        if (existingGroup) {
            return NextResponse.json(
                { success: false, error: "M√£ nh√≥m ƒë√£ t·ªìn t·∫°i" },
                { status: 400 }
            );
        }

        const categoryGroup = new CategoryGroup({
            name: name.trim(),
            code: code.toUpperCase().trim(),
            note: note?.trim() || ""
        });

        await categoryGroup.save();

        return NextResponse.json({
            success: true,
            data: categoryGroup,
            message: "T·∫°o nh√≥m h·∫°ng m·ª•c th√†nh c√¥ng"
        });
    } catch (error) {
        console.error("Error creating category group:", error);
        return NextResponse.json(
            { success: false, error: "Kh√¥ng th·ªÉ t·∫°o nh√≥m h·∫°ng m·ª•c" },
            { status: 500 }
        );
    }
}
